body {
  background-color: #faebd7;
  margin: 20px 15px;
  font-family: Roboto, sans-serif;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin-top: 75px;
  margin-left: auto;
  margin-right: auto;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

img {
  object-fit: cover;
  max-width: 350px;
}

h2 {
  text-align: center;
  margin-top: 10px;
  margin-bottom: 0;
  font-size: 25px;
}

p {
  text-align: center;
  margin-top: 8px;
  font-size: 17px;
  font-weight: 500;
}

.breed-select {
  border: bisque;
  border-radius: 10px;
  padding: 8px 10px;
}

.select-wrapper {
  position: absolute;
  top: 20px;
}

.temperament {
  font-size: 19px;
  font-weight: 600;
}

.wrapper {
  margin-top: 75px;
}

.img-wrap {
  width: 100%;
  height: 100%;
  justify-content: center;
  margin-top: 15px;
  display: flex;
}

